<template>

  <div>
    <h1 class="centralizado">Cadastro</h1>
    <h2 class="centralizado"></h2>

    <h2 v-if="foto._id" class="centralizado">Alterando</h2>
    <h2 v-else class="centralizado">Incluindo</h2>

    <form @submit.prevent="grava()">
      <div class="controle">
        <label for="titulo">T√çTULO</label>
        <input name="titulo" v-model="foto.titulo" id="titulo" autocomplete="off" 
        v-validate data-vv-rules="required|min:3|max:30" >
         <span class="erro" v-show="errors.has('titulo')">{{ errors.first('titulo') }}</span>
      </div>

      <div class="controle">
        <label for="url">URL</label>
        <input name="url" id="url" autocomplete="off" 
        v-model="foto.url" v-validate data-vv-rules="required">
        <span class="erro" v-show="errors.has('url')">{{ errors.first('url') }}</span>
        <imagem-responsiva v-show="foto.url" :url="foto.url" :titulo="foto.titulo" />
      </div>

      <div class="controle">
        <label for="descricao">DESCRI√á√ÉO</label>
        <textarea id="descricao" autocomplete="off" v-model="foto.descricao">
        </textarea>
      </div>

      <div class="centralizado">
        <meu-botao titulo="GRAVAR" tipo="submit"/>
        <router-link :to="{name: 'home'}"><meu-botao titulo="VOLTAR" tipo="button"/></router-link>
      </div>

    </form>
  </div>
</template>

<script>

import ImagemResponsiva from '../shared/ImagemResponsiva/index.vue'
import Botao from '../shared/Botao/index.vue';
import Foto from '../../domain/Foto/index';
import FotoService from '../../domain/Foto/service';

export default {

  components: {

    'imagem-responsiva': ImagemResponsiva, 
    'meu-botao': Botao
  },

  data() {
      return {
          foto: new Foto(),
          id: this.$route.params.id
      }
  },

  methods: {
    grava() {
      this.$validator.validateAll()
        .then(success => {
          if (success) {
            this.service.cadastra(this.foto)
              .then(() => {
                if (this.id) {
                  this.$router.push({ name: 'home' })
                }
                this.foto = new Foto(), err => console.log(err)
              });
            this.foto = new Foto();
          }
        }
      );
    }
  },

  created() {
    this.service = new FotoService(this.$resource);

    if (this.id) {
      this.service.busca(this.id)
        .then(foto => this.foto = foto);
    }
  }
}

</script>
<style scoped>

  .centralizado {
    text-align: center;
  }
  .controle {
    font-size: 1.2em;
    margin-bottom: 20px;

  }
  .controle label {
    display: block;
    font-weight: bold;
  }

 .controle label + input, .controle textarea {
    width: 100%;
    font-size: inherit;
    border-radius: 5px
  }

  .centralizado {
    text-align: center;
  }

  .erro {
    color: red;
  }

</style>


